import { useState, useRef, useEffect } from "react";
import { ReactComponent as ClockSvg } from '../shapes/clock.svg';

function preventDefault(e) {
    e.preventDefault()
}

var supportsPassive = false;

try {
    window.addEventListener("test", null, Object.defineProperty({}, 'passive', {
        get: function () { supportsPassive = true; }
    }));
} catch (e) { }

var wheelOpt = supportsPassive ? { passive: false } : false;

const Timeline = ({ setIndex }) => {
    const [hold, setHold] = useState(false);
    const [pos, setPos] = useState(0);
    const [height, setHeight] = useState(0);
    const [dates, setDates] = useState({
        end: 'Drag the clock',
        start: ''
    })
    const [sectionNum, setSectionNum] = useState(0)
    const clockRef = useRef();
    const timelineRef = useRef();
    const [timelineDates, setTimelineDates] = useState([])

    useEffect(() => {
        let limit = timelineRef.current.offsetHeight - 20
        setHeight(limit);

        let sectionNum = limit / 10;
        setSectionNum(sectionNum)

        let timelineDatesArr = []
        for (let i = 1; i <= 10; i++) {
            let currentPos = i * sectionNum;
            timelineDatesArr.push(currentPos);
        }

        setTimelineDates(timelineDatesArr);
    }, [])

    const onMouseDown = () => setHold(true)

    const onMouseUp = () => setHold(false)

    const onMouseLeave = () => setHold(false);

    const onTouchStart = () => {
        setHold(true)
        window.addEventListener('touchmove', preventDefault, wheelOpt);
    }

    const onTouchEnd = (e) => {
        window.removeEventListener('touchmove', preventDefault, wheelOpt);
        setHold(false)
    }

    const onTouchMove = (e) => {
        if (!hold) return;

        if (e.targetTouches.length === 1) {
            var touch = e.targetTouches[0];
            let diff = touch.pageY - timelineRef.current.offsetTop;

            updateText(diff)
            if (diff < height && diff > 0) {
                setPos(diff)
            }
        }
    }

    const onMouseMove = ({ pageY }) => {
        if (!hold) return;
        //get the difference in position of mouse;
        let diff = pageY - timelineRef.current.offsetTop;

        updateText(diff)

        if (diff < height && diff > 0) {
            setPos(diff - 10)
        }
    }

    const updateText = (diff) => {
        if (diff >= sectionNum * 10) {
            setIndex(10)
            setDates({
                end: 'End',
                start: ''
            })
        } else if (diff >= sectionNum * 9) {
            setIndex(9)
            setDates({
                end: 'December 2017',
                start: 'August 2013'
            })
        } else if (diff >= sectionNum * 8) {
            setIndex(8)
            setDates({
                end: 'May 2017',
                start: 'August 2013'
            })
        } else if (diff >= sectionNum * 7) {
            setIndex(7)
            setDates({
                end: 'August 2016',
                start: 'May 2016'
            })
        } else if (diff >= sectionNum * 6) {
            setIndex(6)
            setDates({
                end: 'May 2017',
                start: 'January 2017'
            })
        } else if (diff >= sectionNum * 5) {
            setIndex(5)
            setDates({
                end: 'December 2017',
                start: 'June 2017'
            })
        } else if (diff >= sectionNum * 4) {
            setIndex(4)
            setDates({
                end: 'October 2018',
                start: 'May 2018'
            })
        } else if (diff >= sectionNum * 3) {
            setIndex(3)
            setDates({
                end: 'September 2019',
                start: 'May 2019'
            })
        } else if (diff >= sectionNum * 2) {
            setIndex(2)
            setDates({
                end: 'October 2020',
                start: 'September 2019'
            })
        } else if (diff >= sectionNum) {
            setIndex(1)
            setDates({
                end: 'Present',
                start: 'June 2021'
            })
        } else {
            setIndex(0)
            setDates({
                end: 'Drag the clock',
                start: ''
            })
        }
    }

    return (
        <div className='timeline'
            onMouseLeave={onMouseLeave}
            onMouseMove={onMouseMove}
            onTouchMove={onTouchMove}
        >
            <div className="timeline-line" ref={timelineRef}>
                <div className='timeline-clock'
                    onMouseDown={onMouseDown}
                    onMouseUp={onMouseUp}
                    onTouchStart={onTouchStart}
                    onTouchEnd={onTouchEnd}
                    ref={clockRef}
                    style={{ top: pos }}
                >
                    <ClockSvg />
                    <div className="timeline-dates">
                        <p className="timeline-dates__end">
                            {dates.end}
                        </p>
                        <p className="timeline-dates__start">
                            {dates.start}
                        </p>
                    </div>
                </div>
                {timelineDates.map(d => (
                    <div className='timeline-section' style={{ top: d - 5 }} />
                ))}
            </div>
        </div>
    )
}

export default Timeline;